header{position:fixed;z-index:8;top:0;left:0;height:120px;width:100%}header nav{font-size:14px;text-transform:uppercase;position:absolute;top:3.5vh;right:3vw}header nav ul{position:relative;display:-webkit-box;display:-webkit-flex;display:-ms-flexbox;display:flex;-webkit-flex-wrap:wrap;-ms-flex-wrap:wrap;flex-wrap:wrap;padding:0;list-style:none}header nav ul a{color:#fff;display:inline-block;width:100%;height:100%;-webkit-transition:text-shadow .3s;transition:text-shadow .3s}header nav ul a:hover{text-shadow:0 0 12px rgba(255,255,255,0.7)}header nav ul .nav__item:nth-child(1).nav__item--current ~ .nav__line{-webkit-transform:translate3d(0%, 0, 0);transform:translate3d(0%, 0, 0)}header nav ul .nav__item:nth-child(2).nav__item--current ~ .nav__line{-webkit-transform:translate3d(200%, 0, 0);transform:translate3d(200%, 0, 0)}header nav ul .nav__item:nth-child(3).nav__item--current ~ .nav__line{-webkit-transform:translate3d(400%, 0, 0);transform:translate3d(400%, 0, 0)}header nav ul .nav__item:nth-child(4).nav__item--current ~ .nav__line{-webkit-transform:translate3d(600%, 0, 0);transform:translate3d(600%, 0, 0)}header .nav__item{cursor:pointer;margin:0;width:80px;height:28px;text-align:center;-webkit-transition:color 0.3s;transition:color 0.3s}header .nav__line{position:absolute;top:100%;left:20px;width:40px;height:2px;pointer-events:none;background:#fff;-webkit-transition:-webkit-transform 0.5s;transition:-webkit-transform 0.5s;transition:transform 0.5s;transition:transform 0.5s, -webkit-transform 0.5s;-webkit-transition-timing-function:cubic-bezier(0.63, -0.48, 0.43, 1.47);transition-timing-function:cubic-bezier(0.63, -0.48, 0.43, 1.47)}.header-transition{-webkit-transform:translateY(0);transform:translateY(0);-webkit-transition:-webkit-transform 1.5s ease;transition:-webkit-transform 1.5s ease;transition:transform 1.5s ease;transition:transform 1.5s ease, -webkit-transform 1.5s ease}.header-enter,.header-leave{-webkit-transform:translateY(-150%);transform:translateY(-150%)}



