footer{color:#fff;position:absolute;width:100%;bottom:20px;text-align:center;font-size:1.1vw}footer svg{fill:#fff;width:1.4vw;height:1.4vw;min-width:14px;min-height:14px;margin:0 10px;-webkit-transition:fill .3s ease;transition:fill .3s ease}#heart{margin:0 2px;width:1vw;height:1vw;min-width:10px;min-height:10px;-webkit-animation:heart-animation .3s infinite alternate;animation:heart-animation .3s infinite alternate}#github svg:hover{fill:#161614}#twitter svg:hover{fill:#1da1f2}@-webkit-keyframes heart-animation{to{-webkit-transform:scale(1.3, 1.3);transform:scale(1.3, 1.3)}}@keyframes heart-animation{to{-webkit-transform:scale(1.3, 1.3);transform:scale(1.3, 1.3)}}



